<style>
	:global(.svelte-emoji-picker) {
		background: #2c2c2c;
		border: 1px solid #7d7d7d;
		width: 16rem;
		/* height: 21rem; */
		z-index: 1;
	}

	:global(.svelte-emoji-picker a) {
		color: rgb(0,100,200);
		text-decoration: none;
	}

	:global(.svelte-emoji-picker a:hover) {
		text-decoration: underline;
	}

	:global(.svelte-emoji-picker a:visited) {
		color: rgb(0,80,160);
	}

	:global(.svelte-emoji-picker input) {
		padding: 0.4em;
		box-sizing: border-box;
		border: 1px solid #ccc;
		border-radius: 2px;
	}

	:global(.svelte-emoji-picker input:disabled) {
		color: #ccc;
	}

	:global(.svelte-emoji-picker input[type="range"]) {
		height: 0;
	}

	:global(.svelte-emoji-picker button) {
		border-color: #7d7d7d;
		outline: none;
	}

	:global(.svelte-emoji-picker button:active) {
		background-color: #7d7d7d;
	}

	:global(.svelte-emoji-picker button:hover) {
		border-color: #fff;
	}

	:global(.svelte-emoji-picker__emoji-tabs) {
		padding: 0.25em;
		height: 15rem;
	}

	:global(.svelte-emoji-picker__emoji-tabs) {
		padding: 0.25em;
		height: 15rem;
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tabs-panel) {
		padding: 0;
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list) {
		display: flex;
		border: none;
	}


	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list .tab) {
		border-right: none;
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list .tab:hover) {
		outline: none;
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list .tab:hover svg path) {
		fill: #fff
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list .tab.selected) {
		background: transparent;
		border-bottom: 2px solid #fff;
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list .tab.selected svg path) {
		fill: #fff
	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tabs-panel) {

	}

	:global(.svelte-emoji-picker__emoji-tabs .tabs .tab-list button) {
		flex-grow: 1;
	}
</style>

<script>
	import {createEventDispatcher} from 'svelte'
	import Icon from '../Icon.svelte'
	import {faBuilding, faFlag, faLightbulb} from '@fortawesome/free-regular-svg-icons'
	import {faSmile, faCat, faCoffee, faFutbol, faHistory, faMusic} from '@fortawesome/free-solid-svg-icons'
	import TabPanel from '../TabPanel.svelte'
	import TabList from '../TabList.svelte'
	import Tabs from '../Tabs.svelte'
	import Tab from '../Tab.svelte'
	import EmojiDetail from './EmojiDetail.svelte'
	import EmojiList from './EmojiList.svelte'
	import EmojiSearch from './EmojiSearch.svelte'
	import EmojiSearchResults from './EmojiSearchResults.svelte'
	import EmojiVariantPopup from './EmojiVariantPopup.svelte'
	import emojiData from './emoji.js'

	export let maxRecents = 50
	export let autoClose = true

	let variantsVisible = false

	let variants
	let currentEmoji
	let searchText
	let recentEmojis = JSON.parse(localStorage.getItem('svelte-emoji-picker-recent')) || []

	const dispatch = createEventDispatcher()

	const emojiCategories = {}

	emojiData.forEach(emoji => {
		let categoryList = emojiCategories[emoji.category]

		if (!categoryList) {
			categoryList = emojiCategories[emoji.category] = []
		}

		categoryList.push(emoji)
	});

	const categoryOrder = [
		'Smileys & People',
		'Animals & Nature',
		'Food & Drink',
		'Activities',
		'Travel & Places',
		'Objects',
		'Symbols',
		'Flags'
	]

	const categoryIcons = {
		'Smileys & People': faSmile,
		'Animals & Nature': faCat,
		'Food & Drink': faCoffee,
		'Activities': faFutbol,
		'Travel & Places': faBuilding,
		'Objects': faLightbulb,
		'Symbols': faMusic,
		'Flags': faFlag
	}

	function showEmojiDetails(e) {
		currentEmoji = e.detail
	}

	function onEmojiClick(e) {
		if (e.detail.variants) {
			variants = e.detail.variants
			variantsVisible = true
		} else {
			dispatch('emoji', e.detail.emoji)
			saveRecent(e.detail)

			if (autoClose) {
				hidePicker()
			}
		}
	}

	function onVariantClick(e) {
		dispatch('emoji', e.detail.emoji)
		saveRecent(e.detail)
		hideVariants()

		if (autoClose) {
			hidePicker()
		}
	}

	function saveRecent(emoji) {
		recentEmojis = [emoji, ...recentEmojis.filter(recent => recent.key !== emoji.key)].slice(0, maxRecents)
		localStorage.setItem('svelte-emoji-picker-recent', JSON.stringify(recentEmojis))
	}

	function hideVariants() {
		// We have to defer the removal of the variants popup.
		// Otherwise, it gets removed before the click event on the body
		// happens, and the target will have a `null` parent, which
		// means it will not be excluded and the clickoutside event will fire.
		setTimeout(() => {
			variantsVisible = false
		});
	}
</script>

<div class="svelte-emoji-picker">
	<EmojiSearch bind:searchText={searchText}/>
	{#if searchText}
		<EmojiSearchResults searchText={searchText} on:emojihover={showEmojiDetails} on:emojiclick={onEmojiClick}/>
	{:else}
		<div class="svelte-emoji-picker__emoji-tabs">
			<Tabs initialSelectedIndex={0}>
				<TabList>
					<Tab>
						<Icon icon={faHistory}/>
					</Tab>
					{#each categoryOrder as category}
						<Tab>
							<Icon icon={categoryIcons[category]}/>
						</Tab>
					{/each}
				</TabList>

				<TabPanel>
					<EmojiList name="Recently Used" emojis={recentEmojis} on:emojihover={showEmojiDetails} on:emojiclick={onEmojiClick} />
				</TabPanel>

				{#each categoryOrder as category}
					<TabPanel>
						<EmojiList name={category} emojis={emojiCategories[category]} on:emojihover={showEmojiDetails} on:emojiclick={onEmojiClick} />
					</TabPanel>
				{/each}
			</Tabs>
		</div>
	{/if}

	{#if variantsVisible}
		<EmojiVariantPopup variants={variants} on:emojiclick={onVariantClick} on:close={hideVariants}/>
	{/if}

	<EmojiDetail emoji={currentEmoji}/>
</div>